<div class="hello"><div class="ui row" v-if="!uid &amp;&amp; (!users || toList(users).length == 0)"><div class="sixteen wide column"> <div class="ui huge buttons"><button class="ui orange button" @click="toggleLogin"><i class="lock icon"></i>{{ $t('login.login_to_see_data') }}</button></div></div></div><loader v-else-if="!users || toList(users).length == 0"></loader><select class="ui dropdown" v-else v-model="logic"><option value="newest">{{$t('friends.recent_updates')}}</option><option value="nearest" v-show="(uid &amp;&amp; users[uid] &amp;&amp; users[uid].latlngColumn)">{{$t('friends.nearest')}}</option><option value="learner_habit_nearst" v-show="uid &amp;&amp; users[uid] &amp;&amp; users[uid].learner_habit">{{$t('friends.similiar_interest') }}</option><option value="ask_match_share" v-show="uid &amp;&amp; users[uid] &amp;&amp; users[uid].ask">{{$t('friends.seeking_help')}}</option><option value="share_match_ask" v-show="uid &amp;&amp; users[uid] &amp;&amp; users[uid].share">{{$t('friends.sharer')}}</option><option value="random">{{$t('friends.random_profile')}}</option><option value="age_nearest" v-if="uid &amp;&amp; users[uid] &amp;&amp; users[uid].child_birth">{{$t('friends.similar_age_kid')}}</option><option v-for="(c, idx) in cities" :value="'near_' + c.c.join(',')" :key="idx">{{c.t}}{{ $t('friends.area') }}</option></select><br><!--.ui.divider--><br><div class="ui two doubling cards container" @scroll="handleScroll" ref="scrollContainer"><div class="ui card" v-for="(h, index) in visibleCards" :key="index"><card :h="h" :full="false" :mySearch="mySearch" :book="book" @locate="locate" @addBook="addBook" @removeBook="removeBook" :uid="uid"></card></div></div></div>